import styled from "styled-components";

/**
 * PersonBox > PersonItem > img, div, p
 */
export const PersonBox = styled.div`
    display: flex;
    flex-wrap: wrap;
    padding: 0 10%;
`;

export const PersonItem = styled.div`
    width: 18.75%;
    height: 0;
    padding-bottom: 18.75%;
    position: relative;
    margin-bottom: 0;
    display: flex;
    overflow: hidden;
    &:nth-child(4n) {
        margin-right: 8%;
    }
    &:not(:nth-child(4n)) {
        margin-right: 3%;
    }
    &:nth-child(4n - 3) {
        margin: 10px 3% 0 8%;
    }

    &:nth-last-child(1),
    &:nth-last-child(2),
    &:nth-last-child(3),
    &:nth-last-child(4) {
        margin-bottom: 60px;
    }

    &:not(:nth-child(1)),
    &:not(:nth-child(2)),
    &:not(:nth-child(3)),
    &:not(:nth-child(4)) {
        margin-top: 10px;
    }

    &:hover {
        & > div,
        & > img {
            & + div {
                opacity: 1;
            }
        }
        & > p {
            bottom: 0;
        }
    }

    & > div,
    & > img {
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        width: 100%;
        height: 100%;
        border-radius: 50%;
        background-color: #b6b6b6;

        & + div {
            position: absolute;
            top: 0;
            left: 0;
            right: 0;
            bottom: 0;
            width: 100%;
            height: 100%;
            border-radius: 50%;
            background-color: rgba(255, 255, 255, 0.7);
            font-size: 15px;
            opacity: 0;
            filter: alpha(opacity=0);
            -webkit-transition: opacity 0.3s;
            transition: opacity 0.3s;
        }
    }

    & > p {
        position: absolute;
        left: 0;
        bottom: -100%;
        width: 100%;
        height: 100%;

        display: flex;
        align-items: center;
        justify-content: center;

        text-align: center;
        font-size: 16px;
        font-weight: bold;
        font-style: normal;
        font-stretch: normal;
        line-height: normal;
        letter-spacing: normal;
        color: #000000;

        transition: bottom 0.3s;
    }
`;

export const PersonLabel = styled.div`
    width: 100%;
    height: 32px;
    font-size: 18px;
    font-weight: bold;
    font-style: normal;
    font-stretch: normal;
    line-height: 1.61;
    letter-spacing: normal;
    color: #000000;
    margin-bottom: 0 !important;
    display: flex;
    align-items: center;
`;



